import san , { DataTypes } from 'san';

const App = san.defineComponent({
    template: `
			<main>
				<form action="">
					<input type="text" id="name" placeholder="" value="{= name =}">
					<input type="number" id="age" placeholder="" value="{= age =}" >
					<input type="text" id="des" placeholder="" value="{= des =}">
				</form>
				<p>{{ name }}</p>
				<p>{{ age | isZero}}</p>
				<p>{{ des }}</p>
				<button on-click="reset"></button>
			</main>
		`,
    initData:() => ({
        name: 'Eric',
        age: 21,
        des: 'hdu'
    }),
    filters:{
        isZero: val => val==0 ? '' : val
    },
    reset:function(data) {
        this.data.set('name','')
        this.data.set('age',0)
        this.data.set('des','')
    }
})

const app = new App()

app.attach(document.body)